
#HeadStyle{
    height: 30px;
    padding: 20px;
    margin:30px;
    font-size: 55px;
    color: rgb(31, 22, 22);
    font-family: 'Righteous', cursive;
}

#Main_Container{
    color: rgb(63, 58, 58);
}

#Pomo_styling{
    display: inline-block;
    width: 600px;
    height: 700px;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    background-image: url(https://ext.decowunder-tapeten.de/out/pictures//master/product/1/Tapete_50_Shades_48467G.jpg);
}

#Container{
    margin-top: 20px;
    padding: 20px;
    font-size: 35px;
    font-family: 'Righteous', cursive;
}

#Break_Length{
    width:300px;
    -ms-flex-pack: left;
        justify-content: left;
    text-align: center;
    
}

#Session{
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    font-size: 35px;
    height:160px;
    border-width: 5px;
    border-color: black;
    border-style: solid;
    border-radius:50px;
}

#Sess2{
    margin-top: 20px;
    margin-bottom: 5px;
}

#timer{
    font-size: 55px;
}

#footer{
    text-align: center;
    font-size: 25px;
    color: rgb(82, 78, 78);
    margin-top:10px;
    font-family: 'Patrick Hand', cursive;
}

.borderc{
    padding: 5px;
  
}
.btn{
    background-image: url(https://ext.decowunder-tapeten.de/out/pictures//master/product/1/Tapete_50_Shades_48467G.jpg);
    border: none; 
    cursor: pointer;
}
.ses_pad{
    padding: 1px;
    
    
}
.bvkp{
    border-width: 1px;
    border-color: black;
    border-style: solid;
}







